import { Component, OnInit, Input } from '@angular/core';
import * as d3 from 'd3';

@Component({
  selector: 'suku-tree',
  templateUrl: './suku-tree.component.html',
  styleUrls: ['./suku-tree.component.scss']
})
export class SukuTreeComponent implements OnInit {
  @Input('tree-data') treeDataFromApi;
  // trace tree
  tree;
  root;
  i;
  svg;
  boxWidth = 180;
  boxHeight = 55;
  margin = {
    top: 50,
    right: 5,
    bottom: 50,
    left: 5
  };
  width = 1360 - this.margin.left - this.margin.right;
  height = 610 - this.margin.top - this.margin.bottom;
  data: any;
  ngOnChanges() {
    console.log('called');
    if (this.treeDataFromApi) {
      console.log('data>', this.treeDataFromApi);
      this.constructTree();
    }
  }

  constructTree() {
    const treeData = JSON.parse(this.treeDataFromApi);
    this.i = 0;
    if (treeData) {
      this.tree = d3.layout
        .tree()
        .separation((a, b) => (a.parent === b.parent ? 5 : 5.25))
        .children((d) => {
          return d.relations;
        })
        .size([this.height, this.width]);

      this.svg = d3
        .select('.tree')
        .append('svg')
        .attr('width', this.width + this.margin.right + this.margin.left)
        .attr('height', this.height + this.margin.top + this.margin.bottom)
        .append('g')
        .attr('transform', 'translate(' + this.margin.left + ',' + this.margin.top + ')');

      this.root = treeData[0];
      this.drawTree();
    }
  }

  drawTree() {
    const nodes = this.tree.nodes(this.root).reverse();
    const links = this.tree.links(nodes);

    nodes.forEach((d) => {
      d.y = this.width - 109 - d.depth * 270; // reverse tree
    });

    const node = this.svg.selectAll('g.node').data(nodes, (d) => {
      return d.id || (d.id = ++this.i); // assign id to nodes
    });

    const nodeEnter = node.enter().append('g').attr('class', 'node').attr('transform', (d) => {
      return 'translate(' + d.y + ',' + d.x + ')'; // append g with class attr
    });

    // append line into svg
    node
      .append('line')
      .style('stroke', (d) => {
        if (d.level === 1) {
          return '#6f3bda';
        }
        if (d.level === 2) {
          return '#3fdbef';
        }
        if (d.level === 3) {
          return '#b8ce2b';
        } else {
          return '#3fdbef';
        }
      })
      .attr('x1', -(this.boxWidth / 2))
      .attr('y1', -(this.boxHeight / 2))
      .attr('x2', -(this.boxWidth / 2))
      .attr('y2', this.boxHeight / 2)
      .attr('stroke-width', '10');

    // append rect into svg
    node
      .append('rect')
      .attr({
        x: -(this.boxWidth / 2),
        y: -(this.boxHeight / 2),
        width: this.boxWidth,
        height: this.boxHeight
      })
      .attr('fill', 'white')
      .attr('stroke-width', '.07')
      .attr('stroke', 'black');

    node
      .append('text')
      .attr('x', (d) => {
        return -(this.boxWidth / 20);
      })
      .attr('y', (d) => {
        return this.boxWidth / 60;
      })
      .style('text-anchor', 'middle')
      .text((d) => {
        return d.product.name;
      });

    const link = this.svg.selectAll('path.link').data(links, (d) => {
      this.data = d;
      return d.target.id;
    });

    // append link into svg
    link
      .enter()
      .insert('path', 'g')
      .attr('class', 'link')
      .attr('stroke', '#000')
      .attr('shape-rendering', 'crispEdges')
      .attr('d', (d) => {
        const sourceX = d.source.x,
          sourceY = d.source.y + this.boxWidth / 2,
          targetX = d.target.x,
          targetY = d.target.y - this.boxWidth / 2;
        return (
          'M' +
          sourceY +
          ',' +
          sourceX +
          'H' +
          (sourceY + (targetY - sourceY) / 2) +
          'V' +
          targetX +
          'H' +
          targetY
        );
      });
  }


  constructor() { }

  ngOnInit() {
  }

}
